// V1

export const getUsernames = async username => {
    const endPoint = `/users/search?username=${username}`;
    const response = await axiosInstance.get(endPoint);
    return response.data.entries;
};

export const getStatsByUsername = async (username, platform = 'pc', window = 'season7') => {
    let endPoint = `/users/id?username=${username}&platform=${platform}&window=${window}`;
    let response = await axiosInstance.get(endPoint);
    const { uid } = response.data;

    endPoint = `/users/public/br_stats?user_id=${uid}&platform=${platform}&window=${window}`;
    response = await axiosInstance.get(endPoint);
    return response.data;
};

// MAKE QUERIES RUN IN PARALLEL
export const getProfileByUsername = async (username, platform = 'pc', window = 'season7') => {
    let endPoint = `/users/id?username=${username}&platform=${platform}&window=${window}`;
    let response = await axiosInstance.get(endPoint);
    const { uid } = response.data;

    // const responseMatches = getMatchesByUid(uid);
    // const responseStats = getStatsByUid(uid);

    // method 1
    // response = await Promise.all([responseStats, responseMatches]);
    // const profile = { profile: response[0], matches: response[1] };

    // method 2
    const profile = { profile: await getStatsByUid(uid), matches: await getMatchesByUid(uid) };

    return profile;
};

export const getProfileByUid = async (uid, platform = 'pc', window = 'season7') => {
    const endPoint = `/users/public/br_stats?user_id=${uid}&platform=${platform}&window=${window}`;
    const response = await axiosInstance.get(endPoint);
    return response.data;
};

export const getNews = async (language = 'en') => {
    const endPoint = `/br_motd/get?language=${language}`;
    const response = await axiosInstance.get(endPoint);
    return response.data;
};

export const getMatchesByUid = async uid => {
    const endPoint = `/users/public/matches_v2?user_id=${uid}`;
    console.log('getting matches');

    const response = await axiosInstance.get(endPoint);

    var promise = new Promise(function(resolve, reject) {
        window.setTimeout(function() {
            resolve(response);
        }, 5000);
    });
    await promise;
    console.log('got matches');
    return promise;

    // const response = await axiosInstance.get(endPoint);
    // console.log('got matches');
    // return response.data;
};

export const getStatsByUid = async (uid, platform = 'pc', window = 'season7') => {
    const endPoint = `/users/public/br_stats?user_id=${uid}&platform=${platform}&window=${window}`;
    console.log('getting stats');
    const response = await axiosInstance.get(endPoint);
    console.log('got stats');
    return response.data;
};
